import React from "react";
import Layout from "../components/layout";
// import Banner from "../screens/main/main_v2/banner";
// import AboutUs from "../screens/main/main_v2/about";
// import OurService from "../screens/main/main_v2/our-service";
// import OurDevelopment from "../screens/main/main_v2/our-development";
// import RecentWorks from "../screens/main/main_v2/recent-works";
// import OurTeam from "../screens/main/main_v2/our-team";
// import MoveForward from "../components/common/move-forward";
import Seo from "../components/seo";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
// import CtaButton from "../components/common/contactbutton";
// import Faq from "../components/common/faq";
// import Achievement from "../screens/main/main_v2/achievement";
import { Suspense } from "react";


const Banner = React.lazy(() => import("../screens/main/main_v2/banner"))
const AboutUs = React.lazy(() => import("../screens/main/main_v2/about"))
const Achievement = React.lazy(() => import("../screens/main/main_v2/achievement"))
const OurService = React.lazy(() => import("../screens/main/main_v2/our-service"))
const RecentWorks = React.lazy(() => import("../screens/main/main_v2/recent-works"))
const OurTeam = React.lazy(() => import("../screens/main/main_v2/our-team"))
const MoveForward = React.lazy(() => import("../components/common/move-forward"))
const CtaButton = React.lazy(() => import("../components/common/connect-button"))
const Faq = React.lazy(() => import("../components/common/faq"))
const OurDevelopment = React.lazy(() => import("../screens/main/main_v2/our-development"))

const IndexPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);

  const faqData = [
    {
      question: "What services does Justtry Technologies offer?",
      answer:
        "Blockchain, DeFi, Wallet, Web2, Web3, Exchange, NFT development.",
    },
    {
      question: "What makes Justtry Technologies stand out?",
      answer:
        "Leading software development company specializing in cutting-edge technologies.",
    },
    {
      question: "Can Justtry Technologies handle diverse projects?",
      answer:
        "Yes, we excel in blockchain, DeFi, wallets, web development, and NFTs.",
    },
    {
      question: "Why choose Justtry Technologies for software development?",
      answer:
        "Expertise in building innovative solutions for blockchain and decentralized technologies.",
    },
  ];

  return (
    <Suspense fallback={<div>Loading</div>}>
      <div className="landing_page">
        <Layout db={db}>
          <Banner db={db} />
          <AboutUs db={db} />
          <Achievement db={db} />
          <OurService db={db} />
          <OurDevelopment />
          {/* <CtaButton db={db} /> */}
          <RecentWorks />
          <OurTeam />
          <Faq faqData={faqData} />
          <MoveForward db={db} />
        </Layout>
      </div>
    </Suspense>
  );
};

export const Head = (props) => {
  const { data } = props;
  const logoObj = data?.allImageSharp?.edges?.find((image) => {
    return image?.node?.gatsbyImageData?.images?.fallback?.src?.includes(
      "new_logo.png"
    );
  });
  return (
    <Seo
      title="Blockchain Development Company | Justtry Technologies"
      metaTitle="Blockchain Development Company | Blockchain Development Services"
      description="Justtry Technologies, a trailblazing software development company, pioneers cutting-edge solutions for diverse industries. With a focus on innovation and client satisfaction, we deliver bespoke software that transforms businesses. Our expertise spans web and mobile applications, AI, and more, ensuring seamless integration of technology to drive success."
      image={logoObj?.node?.gatsbyImageData?.images?.fallback?.src}
    ></Seo>
  );
};

export default IndexPage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
  }
`;
